
.em {
  display: inline-block;
  width: 1em;
}
.em_5 {
  display: inline-block;
  width: .5em;
}
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
input,
form {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
body {
  background-color: #f0eff6;
  color: #372f2c;
  font-family: "Microsoft Yahei";
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#purse .balance {
  overflow: hidden;
  padding: 30px 20px;
  background: #71717b;
  color: #fff;
}
#purse .balance .fl .sty .num {
  font-size: 26px;
}
#purse .balance .fr {
  margin-top: 20px;
}
#purse .gains {
  background: #fff;
  padding: 20px;
  overflow: hidden;
}
#purse .gains .oth {
  margin-top: 10px;
  overflow: hidden;
}
#purse .gains .oth span {
  padding: 3px 10px;
}
#purse .gains .oth .fr {
  margin-right: 20px;
  background: #0f0;
  border-radius: 5px;
  color: #fff;
}
#purse .intr {
  padding: 20px;
}



